// GENERAL CSS SETTINGS

::placeholder {
  color: $color-text-lighter;
}

::selection {
  background-color: $color-accent;
  color: $color-text-lighter;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

iframe {
  border: none;
  display: block;
}
