  max-width: 600px;
  align-items: center;
  margin: 5px auto 15px;
  font-size: 16px;
  font-weight: bold;
  display: flex;
}

@media screen and (min-width: 768px) {
  .team_title {
    font-size: 40px;
  }
}

.team_box {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 30px;
  padding-left: 0;
  display: flex;
}

.team_card {
  flex-direction: column;
  flex-basis: calc(50% - 10px);
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .team_card {
    flex-basis: calc(33.3333% - 10px);
  }
}

@media screen and (min-width: 1024px) {
  .team_card {
    flex-basis: calc(25% - 15px);
  }
}

.team_photo {
  width: 100%;
  height: 140px;
  object-fit: cover;
  border-radius: 5%;
  margin-bottom: 10px;
  transition: all 1s;
  box-shadow: 3px 3px 10px -2px #000;
}

.team_photo:hover {
  transform: scale(1.1);
}

.team_info {
  align-items: flex-end;
  display: flex;
}

.team_name {
  margin-bottom: 5px;
  font-weight: 500;
}

.team_role {
  color: #ff001b;
}

@media screen and (max-width: 767px) {
  .team_role {
    font-size: 14px;
  }
}

